.account-page {
  --main-section-color: #bbbbbb;
  --background-color: #12a6a6;
  --outside-color: #045959;
  --font-color: #12a6a6;
  --border-color: #2c5558;
  --navbar-color: #144245;
  --edit-btn-color: green;
  --delete-btn-color: red;
  --critical-color: red;
  --card-body-color: #a7d4d9;
  --card-head-color: #109595;
}

.account-page * {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.account-page button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.account-page #navbar li {
  padding-bottom: 10px;
  padding-top: 10px;
  background-color: var(--navbar-color);
  list-style-type: none;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.account-page .accountItem ul {
  list-style-type: none;
}

.account-page #navbar a {
  color: var(--font-color);
  text-decoration: none;
}

.account-page body {
  background-color: var(--outside-color);
}

.account-page #content-section {
  background-color: var(--background-color);
  border: var(--border-color) 1px solid;
}

.account-page #content-section h1 {
  margin-bottom: 30px;
}

.account-page .accountItem {
  display: flex;
  margin: 30px;
}

.account-page .accountItem h3,
.account-page .accountItem button {
  margin-right: 10px;
}

.account-page #footer-div {
  background-color: var(--outside-color);
  padding: 10px;
  margin-top: 2%;
}

@media (min-width: 900px) {
  .account-page #nav {
    display: flex;
    justify-content: space-evenly;
  }

  .account-page #navbar ul {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    list-style-type: none;
  }

  .account-page body {
    display: flex;
    flex-direction: column;
  }

  .account-page #content-section {
    height: 98vh;
    margin-top: 2%;
    margin-left: 0.5%;
    margin-right: 0.5%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 900px) {
  .account-page header {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .account-page #navbar {
    display: none;
  }

  .account-page #nav {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .account-page #navbar ul {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  .account-page #content-section {
    height: 98vh;
    margin-top: 2%;
    margin-left: 0.5%;
    margin-right: 0.5%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
